import { render, useState, useEffect } from "@wordpress/element";
import domReady from "@wordpress/dom-ready";
import apiFetch from '@wordpress/api-fetch';

const WPTravelDemoPage = () => {

	const [demoList, setDemoList] = useState([]);
	const [loading, setLoading] = useState( true );
	const [modalOpen, setModalOpen] = useState( false );
	const [updateDemoList, setUpdateDemoList] = useState( true );
	const [demoToImport, setDemoToImport] = useState('')
	const [importing, setImporting] = useState(false)
	const [error, setError] = useState('')
	const [importNotice, setImportNotice] = useState(false)
	const [isNoticeEnable, setIsNoticeEnable] = useState(false);

	const confirmImport = ( fileUrl ) => {
		setModalOpen(true)
		setDemoToImport(fileUrl)
	}

	const importDemo = ( fileUrl ) => {
		setLoading(false);
		setImporting( true )

		apiFetch( { path: '/wptraveldemo/v1/import/'+fileUrl, method: 'POST' } ).then( ( response ) => {
			if( response == "missing_wpcf7" ) {
				console.log("Please install and activate Contact Form 7 plugin before you proceed.")
				setError(true)
				setIsNoticeEnable(true);
				setImportNotice( <>Please install and activate <a href='https://wordpress.org/plugins/contact-form-7/' target='new'>Contact Form 7</a> plugin before you proceed.</> );
				setTimeout(() => {
					setIsNoticeEnable( false );
				}, 5000 )
			} else {
				setError(false)
				setIsNoticeEnable(true);
				setImportNotice( "Demo Imported Successfully!" )
				setTimeout(() => {
					setIsNoticeEnable(false);
				}, 5000 )
			}
			setImporting( false )
		} ).catch( ( error ) => {
			console.log( "An unexpected error occurred! ", error )
			setError(true)
			setIsNoticeEnable(true);
			setImportNotice("An unexpected error occurred!");
			setTimeout(() => {
				setIsNoticeEnable( false );
			}, 5000 )
			setImporting( false )
		});
		setModalOpen(false)
	}
	useEffect( () => {
		apiFetch( { path: '/wptraveldemo/v1/get_demo_list', method: 'GET' } ).then( ( response ) => {
			setLoading(false);
			setDemoList( response );
			setUpdateDemoList( !updateDemoList );
		} ).catch( ( error ) => console.log( error ) );
	}, [] );

	return  (
        <div id="wp-travel-demo-collection">
			{ modalOpen && <Modal close={ () => setModalOpen(false) } importDemo={ () => importDemo( demoToImport ) } /> }
			<span id="import-note"><span class="dashicons dashicons-info"></span> Make sure <a href='https://wordpress.org/plugins/contact-form-7/' target='new'>Contact Form 7</a> is active and site is fresh before import.</span>
			{ isNoticeEnable && <div id="import-notice" className={`${error ? "error" : "success"}`}>
				<div className="wp-travel-notice-icon">
					{error ? <span class="dashicons dashicons-no-alt"></span> : <span class="dashicons dashicons-saved"></span>}
				</div>
				<div className="wp-travel-notice-text"><span>{importNotice}</span></div>
			</div> }
			{ importing && <div id="loader">
				<Loader />
				<p>Importing Demo ... Please wait this may take a while</p>
			</div> }
			{ loading && !importing && <Loader /> }
			{ !loading && !importing && <div id="wp-travel-demo-lists" className="container">
				{ demoList.map(({screenshot, text_domain, demo_name}) => {
					let updatedTextDomain = text_domain.replaceAll("_", "-")
					return (
						<div className="wp-travel-demo-card">
							<img src={screenshot}/>
							<div className="wp-travel-demo-card-details-container">
								<span className="wp-travel-demo-title">{demo_name}</span>
								<div className="wp-travel-demo-card-btn-container">
									<a href={ "https://wpdemo.wensolutions.com/" + updatedTextDomain } className="wp-travel-demo-card-btn secondary" target="_blank">View Demo</a>
									<a href="#" onClick={ () => {
										confirmImport( text_domain )
									}} className="wp-travel-demo-card-btn primary">Import</a>
								</div>
							</div>
						</div>
					)
				} ) }
			</div> }
        </div>
    )

}

const Modal = ({ close, importDemo }) => {
	return (
		<div class="wp-travel-blocks-modal-wrapper">
			<div class="wp-travel-blocks-modal">
				<div class="wp-travel-blocks-modal-icon-container">
					<span class="dashicons dashicons-info"></span>
				</div>
				<div class="wp-travel-blocks-modal-warning-text">
					<span>Are you sure you want to import this demo?</span>
					<span class="error">This will overwrite all the previous imports.</span>
				</div>
				<div class="wp-travel-blocks-modal-action-btns">
					<button class="wp-travel-blocks-modal-action-btn secondary" onClick={importDemo} >Confirm</button>
					<button class="wp-travel-blocks-modal-action-btn primary" onClick={close}>Go Back</button>
				</div>
			</div>
			<div class="wp-travel-blocks-modal-backdrop" onClick={close}></div>
    	</div>
	)
}

const Loader = () => {
	return (
		<div id="loader">
			<svg width="50px" height="50px" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid">
				<circle cx="50" cy="50" fill="none" stroke="#11ae59" stroke-width="10" r="41" stroke-dasharray="193.20794819577225 66.40264939859075">
					<animateTransform attributeName="transform" type="rotate" repeatCount="indefinite" dur="1s" values="0 50 50;360 50 50" keyTimes="0;1"></animateTransform>
				</circle>
			</svg>
		</div>
	)
}


domReady( () => {
    if (
        "undefined" !== typeof document.getElementById( "wptravel-demo-page" ) &&
        null !== document.getElementById( "wptravel-demo-page" )
    ) {
        render( <WPTravelDemoPage />, document.getElementById( "wptravel-demo-page" ) );
    }
} );
